import { QueryClient } from '@tanstack/react-query';
import { createAsyncStoragePersister } from '@tanstack/query-async-storage-persister';
import Browser from 'webextension-polyfill';
import cache from 'webext-storage-cache';

function createPersister() {
  return {
    setItem: (key: string, value: string) => {
      return Browser.storage.local.set({ [key]: value });
    },
    getItem: async (key: string) => {
      return await Browser.storage.local.get(key).then(res => {
        return res[key];
      });
    },
    removeItem: (key: string) => {
      return Browser.storage.local.remove(key);
    },
  };
}

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
      cacheTime: 1000 * 60 * 60 * 24,
      onError: async err => {
        if (
          (err as any)?.message === 'Forbidden' &&
          !queryClient.isFetching(['user'])
        ) {
          queryClient.cancelQueries();
          await cache.delete('user');
          Browser.storage.local.clear();
          queryClient.invalidateQueries();
        }
      },
      retry: (failureCount, error) => {
        if ((error as any)?.message === 'Forbidden') return false;
        return failureCount <= 3;
      },
    },
  },
});

const persister = createAsyncStoragePersister({ storage: createPersister() });

export { queryClient, persister };
