import { useQuery } from '@tanstack/react-query';

import { sendMessage } from '@src/lib/messaging';
import { OrganizationFeatures } from '@toggl/track-types';

const fetch = () =>
  sendMessage({
    type: 'fetch-user',
    payload: null,
  });

const fetchWorkspaces = () =>
  sendMessage({
    type: 'fetch-workspaces',
    payload: null,
  });

const fetchWorkspacePreferences = (workspaceId: number) =>
  sendMessage({
    type: 'fetch-workspacePreferences',
    payload: {
      workspaceId,
    },
  });

const fetchOrganizationFeatures = () =>
  sendMessage({
    type: 'fetch-organizationFeatures',
    payload: null,
  });

const fetchWorkspaceFeatures = () =>
  sendMessage({
    type: 'fetch-workspaceFeatures',
    payload: null,
  });

export const isFeatureEnabled = (
  feature: string,
  organizationId: number,
  organizationFeatures: OrganizationFeatures[]
) => {
  const currentOrganization = organizationFeatures.find(
    f => f.organization_id === organizationId
  );

  return !!currentOrganization?.features.find(
    feat => feat.name === feature && feat.enabled
  );
};

export const useUser = () => {
  return useQuery(['user'], fetch, {
    retry: false,
    staleTime: 60000 * 60,
    refetchOnWindowFocus: true,
  });
};

export const useWorkspaces = () => {
  return useQuery(['workspaces'], fetchWorkspaces, {
    staleTime: Infinity,
  });
};

export const useWorkspacePreferences = (
  workspaceId: number | null | undefined
) => {
  return useQuery(
    ['workspacePreferences', workspaceId],
    () => fetchWorkspacePreferences(workspaceId ?? 0),
    { staleTime: Infinity, enabled: !!workspaceId }
  );
};

export const useWorkspaceFeatures = () => {
  return useQuery(['workspaceFeatures'], () => fetchWorkspaceFeatures(), {
    staleTime: Infinity,
  });
};

export const useOrganizationFeatures = (enabled = true) => {
  return useQuery(['organizationFeatures'], fetchOrganizationFeatures, {
    staleTime: Infinity,
    enabled,
  });
};
