import { DateTime, Duration } from 'luxon';

import { TimeEntryV9 } from '@toggl/track-types';

const MAX_DURATION = 3596400000; // 999 hours in milliseconds
const HHMMSS_DURATION_REGEX =
  /^\s*(\d+)?[ :;_/-]?([\d,.]+)?:?([[ :;_/-]+[\d,.]+)?\s*$/;
const HUMAN_DURATION_REGEX =
  /^\s*((\d+(?:\.|,)?\d*|\d*(?:\.|,)?\d+)\s*(((m|min|mins|minute|minutes)?)|((h|hr|hrs|hour|hours)?)|((s|sec|secs|second|seconds)?)|((d|ds|day|days)?)|((w|wk|wks|week|weeks)?))?\s*)+$/i;

export const calculateStopDate = (
  start: string,
  duration: number
): string | null =>
  duration < 0
    ? null
    : DateTime.fromISO(start, { setZone: true })
        .plus(duration * 1000)
        .toISO({ includeOffset: true });

export const calculateStartDate = (duration: number): string | null =>
  duration < 0
    ? null
    : DateTime.fromISO(DateTime.now().toISO(), { setZone: true })
        .minus(duration * 1000)
        .toISO({ includeOffset: true });

export function parseTimeDuration(str: string) {
  str = str.replace(/[,.]+/g, '.').replace(/[ :;_/-]+/g, ':'); // 2,5;5 => 2.5:5
  let hms = str.split(':').map(part => parseFloat(part || '0'));

  if (hms.length === 1) {
    if (hms[0] > 99) hms = [Math.floor(hms[0] / 100), hms[0] % 100, 0];
    else hms = [0, hms[0], 0];
  }

  if (hms.length >= 2) hms = [hms[0], hms[1], hms[2] || 0];

  return Duration.fromObject({
    seconds: (hms[0] || 0) * 60 * 60 + (hms[1] || 0) * 60 + (hms[2] || 0),
  });
}

export function parseHumanDuration(inputDuration, options = {}) {
  const input = inputDuration;
  const match = HHMMSS_DURATION_REGEX.exec(input);
  if (match) {
    return parseTimeDuration(input);
  }
}
export function forceMaxDuration(duration: Duration) {
  return duration.toMillis() > MAX_DURATION
    ? Duration.fromMillis(MAX_DURATION)
    : duration;
}

export function parseDuration(str) {
  const duration = isValidHumanDuration(str)
    ? parseHumanDuration(str)
    : parseTimeDuration(str);
  return forceMaxDuration(duration!);
}

export function isValidTimeDuration(duration) {
  return HHMMSS_DURATION_REGEX.test(duration);
}

export function isValidHumanDuration(duration) {
  return HUMAN_DURATION_REGEX.test(duration);
}

export const secToHHMM = (sum: number) => {
  const hours = Math.floor(sum / 3600);
  const minutes = Math.floor((sum % 3600) / 60);

  return hours + 'h ' + minutes + 'm';
};

/** Returns the elapsed time since `since` as a duration in the format hh:mm:ss. */
export const formatDuration = (start: string, stop?: string | null) => {
  const now = stop ? DateTime.fromISO(stop) : DateTime.now();
  const duration = now.diff(DateTime.fromISO(start), [
    'hours',
    'seconds',
    'minutes',
  ]);

  const timeValue = (value: number) => value.toString().padStart(2, '0');

  return `${duration.hours}:${timeValue(duration.minutes)}:${timeValue(
    Number(duration.seconds.toFixed(0))
  )}`;
};

export const calculateSums = (
  timeEntries: TimeEntryV9[],
  beginningOfWeek: number
) => {
  const now = new Date();
  let todaySum = 0;
  let weekSum = 0;

  now.setHours(0);
  now.setMinutes(0);
  now.setSeconds(0);

  // Get today's date at midnight for the local timezone
  const today = new Date();
  today.setHours(0, 0, 0, 0);

  const getWeekStart = function (d: Date) {
    const startDay = beginningOfWeek;
    const day = d.getDay();
    const diff = d.getDate() - day + (startDay > day ? startDay - 7 : startDay);
    return new Date(d.setDate(diff));
  };

  const weekStart = getWeekStart(now);

  timeEntries.forEach(function (entry) {
    // Calc today total
    if (new Date(entry.start).getTime() > today.getTime()) {
      if (entry.duration < 0) {
        const curDuration = Math.floor(
          (new Date().getTime() - new Date(entry.start).getTime()) / 1000
        );
        todaySum += curDuration;
      } else {
        todaySum += entry.duration;
      }
    }

    // Calc week total
    if (new Date(entry.start).getTime() > weekStart.getTime()) {
      if (entry.duration < 0) {
        weekSum +=
          (new Date().getTime() - new Date(entry.start).getTime()) / 1000;
      } else {
        weekSum += entry.duration;
      }
    }
  });

  const todaySince = DateTime.now().minus({ seconds: todaySum }).toISO();
  const weekSince = DateTime.now().minus({ seconds: weekSum }).toISO();

  return { today: formatDuration(todaySince), week: formatDuration(weekSince) };
};
