import * as React from 'react';
import {
  AriaTimeFieldProps,
  useDateSegment,
  useTimeField,
} from '@react-aria/datepicker';
import { DateTime } from 'luxon';
import {
  DateSegment as DateSegmentType,
  DateFieldState,
  useTimeFieldState,
} from '@react-stately/datepicker';
import styled from '@emotion/styled';
import { css, useTheme } from '@emotion/react';
import { Time } from '@internationalized/date';

type TimeFieldProps = {
  error?: boolean;
  hourCycle?: 12 | 24;
  onClick?: React.MouseEventHandler;
} & AriaTimeFieldProps<Time>;

type RootProps = {
  error?: TimeFieldProps['error'];
};

export function TimeField(props: TimeFieldProps) {
  const locale = DateTime.now().locale;
  const theme = useTheme();

  const state: DateFieldState = useTimeFieldState({
    ...props,
    locale,
    hourCycle: props.hourCycle,
  });

  const ref: React.RefObject<HTMLDivElement> =
    React.useRef<HTMLDivElement>(null);
  const { fieldProps } = useTimeField(
    { ...props, 'aria-label': 'Time input' },
    state,
    ref
  );

  return (
    <Root error={props.error}>
      <div
        css={css`
          display: flex;
          ${props.isDisabled &&
          css`
            color: ${theme.colors.ui.disabled};
          `}
        `}
        {...fieldProps}
        ref={ref}
        className='field'
        onClick={props.onClick}
      >
        {state.segments.map((segment, i) => (
          <DateSegment key={i} segment={segment} state={state} />
        ))}
        {state.validationState === 'invalid' && (
          <span aria-hidden='true'>{'🚫'}</span>
        )}
      </div>
    </Root>
  );
}

// Note: this comonent is the same as in the useDateField docs.
function DateSegment({
  segment,
  state,
}: {
  segment: DateSegmentType;
  state: DateFieldState;
}) {
  const ref: React.RefObject<HTMLDivElement> =
    React.useRef<HTMLDivElement>(null);
  const { segmentProps } = useDateSegment(segment, state, ref);

  return (
    <div
      {...segmentProps}
      ref={ref}
      className={`segment ${segment.isPlaceholder ? 'placeholder' : ''}`}
    >
      {segment.text}
    </div>
  );
}

const Root = styled.div<RootProps>`
  display: flex;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
  margin: 0 4px;
  padding: 8px;

  border-radius: 8px;
  border: 1px solid
    ${({ error, theme }) =>
      error ? theme.colors.error.regular : theme.colors.ui_40.regular};

  color: ${({ theme }) => theme.colors.ui.regular};
`;
