import { QueryClient, Updater, useQuery } from '@tanstack/react-query';

import { sendMessage } from '@src/lib/messaging';
import { Project, ProjectCreateProperties } from '@toggl/track-types';

const fetch = () =>
  sendMessage({
    type: 'fetch-projects',
    payload: null,
  });

export const create = (payload: {
  project: ProjectCreateProperties;
  workspaceId: number;
}) => {
  return sendMessage({ type: 'create-project', payload });
};

const fetchClients = () =>
  sendMessage({
    type: 'fetch-clients',
    payload: null,
  });

const fetchTasks = () =>
  sendMessage({
    type: 'fetch-tasks',
    payload: null,
  });

export const useProjects = (enabled = true) => {
  return useQuery(['projects'], fetch, {
    staleTime: 5000 * 60,
    enabled,
  });
};

export const useClients = () => {
  return useQuery(['clients'], fetchClients, { staleTime: 5000 * 60 });
};

export const useTasks = () => {
  return useQuery(['tasks'], fetchTasks, { staleTime: 5000 * 60 });
};

export const cancelQuery = (queryClient: QueryClient) => {
  return queryClient.cancelQueries(['projects']);
};

export const getQueryData = (queryClient: QueryClient) => {
  return queryClient.getQueryData<Record<number, Project>>(['projects']);
};

export const setQueryData = (
  queryClient: QueryClient,
  updater: Updater<
    Record<number, Project> | undefined,
    Record<number, Project> | undefined
  >
) => {
  return queryClient.setQueryData(['projects'], updater);
};
