import { SerializedStyles, useTheme } from '@emotion/react';
import styled from '@emotion/styled';

import * as React from 'react';

type Props = {
  className?: string;
  children: React.ReactNode;
  color?: string;
  size?: 'xs' | 'regular' | 'xl';
  textColor?: string;
  uppercase?: boolean;
};

export const Badge: React.FC<Props> = ({
  className,
  children,
  color,
  size = 'regular',
  textColor,
  uppercase = false,
}) => {
  const theme = useTheme();

  return (
    <Root className={className}>
      <Text
        color={color ?? theme.colors.ui_60.regular}
        textColor={textColor ?? theme.colors.ui_20.regular}
        size={size}
        uppercase={uppercase}
      >
        {children}
      </Text>
    </Root>
  );
};

const Root = styled.span`
  display: inline-block;
  width: auto;
`;

const Text = styled.span<Props>`
  line-height: normal;

  display: flex;
  align-items: center;
  justify-content: center;

  height: 20px;
  padding: 1px 5px;
  border-radius: 6px;

  text-transform: ${({ size, uppercase }) =>
    uppercase || size === 'xl' ? 'uppercase' : 'none'};
  color: ${({ textColor }) => textColor};

  background-color: ${({ color }) => color};
`;

export const IconBadge: typeof Badge = styled(Badge)`
  ${Text} {
    > svg {
      margin-right: 5px;
    }

    padding: 1px 12px 1px 9px;
    font-weight: 500;
  }
`;
