import Browser from 'webextension-polyfill';

import { MessageTypes } from '../types';
import * as errorReport from './errorReport';

export async function sendMessage<T extends keyof MessageTypes>(request: {
  type: T;
  payload: MessageTypes[T]['arg'];
}): Promise<MessageTypes[T]['return']> {
  if (process.env.mode === 'development') {
    errorReport.debug('Extension:sendMessage', request);
  }

  try {
    const response = await chrome.runtime.sendMessage(request);
    if (response?.__mozWebExtensionPolyfillReject__)
      throw new Error(response.message);
    if (process.env.DEBUG) {
      errorReport.debug('Extension:sendMessageResponse', request, response);
    }

    return response;
  } catch (error) {
    if (process.env.DEBUG) {
      errorReport.debug('Extension:sendMessageResponse', request, error);
    }
    errorReport.error(error);
    throw error;
  }
}
