import { parseToHsl, hslToColorString } from 'polished';

type GenerateInteractionColorsArgs<T extends string> = {
  base: T;
  theme?: 'light' | 'dark';
};

export type InteractionColors<T extends string> = {
  regular: T;
  hover: string;
  onClick: string;
  disabled: string;
};

export function generateInteractionColors<T extends string>({
  base,
  theme = 'light',
}: GenerateInteractionColorsArgs<T>): InteractionColors<T> {
  const HSL = parseToHsl(base);

  let hover;
  let onClick;
  let disabled;

  if (theme === 'light') {
    hover = hslToColorString({
      hue: HSL.hue,
      saturation: HSL.saturation - HSL.saturation * 0.04,
      lightness:
        Math.max(
          HSL.lightness - (0.11 - 0.09 * HSL.lightness + 0.05 * HSL.saturation),
          0
        ) ||
        HSL.lightness + (0.6 + 0.01 * HSL.lightness - 0.05 * HSL.saturation),
    });

    onClick = hslToColorString({
      hue: HSL.hue,
      saturation: HSL.saturation - HSL.saturation * 0.08,
      lightness:
        Math.max(
          HSL.lightness - (0.15 - 0.09 * HSL.lightness + 0.05 * HSL.saturation),
          0
        ) ||
        HSL.lightness + (0.09 + 0.01 * HSL.lightness - 0.05 * HSL.saturation),
    });

    disabled = hslToColorString({
      hue: HSL.hue,
      saturation: HSL.saturation * 0.6,
      lightness: 0.8 + 0.15 * HSL.lightness,
    });
  } else {
    hover = hslToColorString({
      hue: HSL.hue,
      saturation: (() => {
        const saturation = HSL.saturation - HSL.saturation * 0.06;
        return saturation <= 1
          ? saturation
          : HSL.saturation - HSL.saturation * 0.04;
      })(),
      lightness: (() => {
        const lightness =
          HSL.lightness + (0.07 - 0.05 * HSL.lightness - 0.02 * HSL.saturation);

        return lightness <= 1
          ? lightness
          : HSL.lightness -
              (0.11 - 0.09 * HSL.lightness + 0.05 * HSL.saturation);
      })(),
    });

    onClick = hslToColorString({
      hue: HSL.hue,
      saturation: (() => {
        const saturation = HSL.saturation - HSL.saturation * 0.1;
        return saturation <= 1
          ? saturation
          : HSL.saturation - HSL.saturation * 0.08;
      })(),
      lightness: (() => {
        const lightness =
          HSL.lightness + (0.1 - 0.05 * HSL.lightness - 0.02 * HSL.saturation);

        return lightness <= 1
          ? lightness
          : HSL.lightness -
              (0.15 - 0.09 * HSL.lightness + 0.05 * HSL.saturation);
      })(),
    });

    disabled = hslToColorString({
      hue: HSL.hue,
      saturation: HSL.saturation * 0.4,
      lightness: 0.15 + 0.05 * HSL.lightness,
    });
  }

  return {
    regular: base,
    hover,
    onClick,
    disabled,
  };
}
