import { transparentize } from 'polished';
import * as React from 'react';

import * as color from './color';

export type Theme = {
  name: 'track' | 'classic' | 'dark' | 'MOCK';
  next: typeof color.dsm.light | typeof color.dsm.dark;
  colors: {
    text: string;
    accentText: string;
    whiteText: string;
    background: string;
    foreground: string;
    primary: {
      default: string;
      dark: string;
      darker: string;
      darkest: string;
      light: string;
      lighter: string;
    };
    secondary: string;
    accent: {
      dark: string;
      darky: string;
      darker: string;
      lessDark: string;
      default: string;
      light: string;
      lighter: string;
      lighty: string;
      lessLight: string;
      ultraLight: string;
    };
    muted: string;
    beige: string;
    beigeBackground: string;
    error: string;
    success: string;
    warning: string;
    standoutBackground: string;
    timerButton: {
      start: string;
      startHover: string;
      startActive: string;
      startHalo: string;
      stop: string;
      stopHover: string;
      stopActive: string;
      stopHalo: string;
    };
    timerStart?: string;
    timerStop?: string;
    calendarEventBackdrop: string;
    disabledIcon: string;
  };
};

// If C is omitted, WithTheme<> returns only { theme: Theme }
export type WithTheme<
  C extends keyof JSX.IntrinsicElements | React.JSXElementConstructor<any> = any
> = Omit<React.ComponentPropsWithoutRef<C>, 'theme'> & {
  theme: Theme;
};

// TODO: add theme type, the problem is that theme definition is on main webapp package
export type ThemeProps = WithTheme<React.JSXElementConstructor<unknown>>;

