import * as React from 'react';

import { Db } from '@src/lib/db';
import * as errorReport from '@src/lib/errorReport';

import { DEFAULT_SETTINGS } from '../constants';
import Browser from 'webextension-polyfill';

type SettingsType = typeof DEFAULT_SETTINGS;

export default function useSetting<T extends keyof SettingsType>(
  key: T,
  listen?: boolean
): [SettingsType[T], (value: SettingsType[T]) => void, boolean] {
  const [value, setValue] = React.useState(DEFAULT_SETTINGS[key]);
  const [loaded, setLoaded] = React.useState(false);

  React.useEffect(() => {
    Db.get(key).then(res => {
      setValue(res ?? DEFAULT_SETTINGS[key]);
      setLoaded(true);
    });

    if (listen)
      Browser.storage.onChanged.addListener(changes => {
        if (changes[key]) setValue(changes[key].newValue);
      });
  }, []);

  const update = (newValue: SettingsType[T]) => {
    Db.set(key, newValue)
      .then(() => setValue(newValue))
      .catch(err => errorReport.error(err));
  };

  return [value, update, loaded];
}
