type DSM = typeof dsm;
type DSMColor = {
  regular: string;
  hover?: string;
  onClick?: string;
  disabled?: string;
};

type MockedDSM = DSM & { MOCK: typeof dsm.light };

function generateDSMWithMock(dsm: DSM) {
  const MOCK_COLOR = '#FF00FF' as const;

  const colorMockHandler = {
    get(): typeof MOCK_COLOR {
      return MOCK_COLOR;
    },
  };

  const dsmMockHandler = {
    get(
      target: typeof dsm.light,
      prop: keyof typeof dsm.light
    ): typeof MOCK_COLOR | DSMColor {
      const value = target[prop];
      if (value && typeof value === 'object') {
        return new Proxy(value, colorMockHandler);
      }
      return MOCK_COLOR;
    },
  };

  return { ...dsm, MOCK: new Proxy(dsm.light, dsmMockHandler) };
}

const mockedDSM: MockedDSM = generateDSMWithMock(dsm);

export { mockedDSM as dsm };
