import * as color from '@src/packages/ui/color';

// Mapping of old preset colors new ones
// Conversion needs to be done on the frontend until backend has done this conversion
// as a db migration
const colors: Record<string, { order: number; value: string }> = {};

// order of the keys matters here
colors[color.dwayneJohnson] = {
  order: 1,
  value: color.support.blue,
};
colors[color.allCovered] = {
  order: 2,
  value: color.support.lilac,
};
colors[color.oneMoreThing] = {
  order: 3,
  value: color.support.pink,
};
colors[color.musicalSnake] = {
  order: 4,
  value: color.support.orange,
};
colors[color.iLikeColors] = {
  order: 5,
  value: color.support.darkOrange,
};
colors[color.iHaveNoFriends] = {
  order: 6,
  value: color.support.green,
};
colors[color.dontKnowMuchPeople] = {
  order: 7,
  value: color.support.turquoise,
};
colors[color.noReally] = {
  order: 8,
  value: color.support.salmon,
};
colors[color.minionWorker] = {
  order: 10,
  value: color.support.violet,
};
colors[color.sasaJuric] = {
  order: 11,
  value: color.support.yellow,
};
colors[color.witcher] = {
  order: 12,
  value: color.support.pine,
};
colors[color.fancyCheetah] = {
  order: 9,
  value: color.support.navy,
};
colors[color.snowTurtle] = { order: 13, value: color.support.red };
colors[color.michaelCera] = {
  order: 14,
  value: color.support.darkGrey,
};

export function getAllBasicColors(order = false): string[] {
  let chain = Object.keys(colors);

  if (order) {
    chain = chain.sort((a, b) => (colors[a].order < color[b].order ? -1 : 1));
  }

  return chain.map(k => colors[k].value);
}

export function getAllPaidColors(order = false): string[] {
  return getAllBasicColors(order);
}
