import { ThemeProvider } from '@emotion/react';
import useSetting from '@src/lib/hooks/useSetting';

import { theme } from '@ui/theme';

import * as React from 'react';
import Browser from 'webextension-polyfill';

export const AppThemeProvider: React.FC<{
  children: React.ReactNode;
  enforceColorScheme?: 'dark' | 'light';
}> = ({ children, enforceColorScheme }) => {
  const prefersColorScheme = window.matchMedia('(prefers-color-scheme: dark)')
    .matches
    ? 'dark'
    : 'light';

  const [darkModeUserSet] = useSetting('darkModeUserSet');
  const [activeTheme, setActiveTheme] = React.useState(prefersColorScheme);
  const [darkMode] = useSetting('darkMode');
  const settingsColorScheme = darkModeUserSet
    ? darkMode
      ? 'dark'
      : 'light'
    : prefersColorScheme;

  React.useEffect(
    () => setActiveTheme(settingsColorScheme),
    [settingsColorScheme]
  );

  React.useEffect(() => {
    Browser.storage.sync.onChanged.addListener(changes => {
      if (changes.darkMode)
        setActiveTheme(
          (changes.darkMode as { newValue: boolean })?.newValue
            ? 'dark'
            : 'light'
        );
    });
  }, []);

  return (
    <ThemeProvider theme={theme[enforceColorScheme ?? activeTheme]}>
      {children}
    </ThemeProvider>
  );
};
